import { Form, Input, Select, Row, Col, DatePicker } from "antd";
import { BaseFilter, Qbtn } from "common";
const FormItem = Form.Item;
const { RangePicker } = DatePicker;
const { Option } = Select;
class Search extends BaseFilter {
  constructor(props) {
    super(props);
  }
  render() {
    const { shopId } = this.props;
    console.log(this.props)
    const { getFieldDecorator } = this.props.form;
    return (
      <div className="qtoolOms-condition">
        <Form className="serach-common-form">
          <Row gutter={24}>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout}>
                {getFieldDecorator("orderNo")(
                  <Input placeholder="" autoComplete="off" />
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="SKU" {...this.formItemLayout}>
                {getFieldDecorator("skuCode")(
                  <Input placeholder="SKU" autoComplete="off" />
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout}>
                {getFieldDecorator("consignee")(
                  <Input placeholder="" autoComplete="off" />
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout}>
                {getFieldDecorator("phone")(
                  <Input placeholder="" autoComplete="off" />
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout}>
                {getFieldDecorator("sourceCode")(
                  <Select placeholder="" allowClear={true}>
                    <Option value=""></Option>
                    <Option value="QANDDROID,QIOS,QWCHAT"></Option>
                    <Option value="QPOS,QMANAGER"></Option>
                    <Option value="QPOS">pos</Option>
                    <Option value="QMANAGER"></Option>
                    <Option value="QANDDROID"></Option>
                    <Option value="QIOS">ios</Option>
                    <Option value="QWCHAT"></Option>
                  </Select>
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout}>
                {getFieldDecorator("orderStatus")(
                  <Select placeholder="" allowClear={true}>
                    <Option value=""></Option>
                    <Option value={210}></Option>
                    <Option value={230}></Option>
                    <Option value={999}></Option>
                    <Option value={310}></Option>
                    <Option value={911}></Option>
                    <Option value={320}></Option>
                    <Option value={390}></Option>
                    <Option value={0}></Option>
                  </Select>
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem
                className="select_multiple"
                label="ID"
                {...this.formItemLayout}
              >
                {getFieldDecorator("channelCode", {
                  initialValue: shopId,
                  onChange:this.props.shopIdChange
                })(
                  <Select
                    maxTagTextLength={100}
                    maxTagCount={2}
                    allowClear={true}
                    mode="multiple"
                    placeholder="id"
                  >
                    {shopId.map(item => (
                      <Option key={item} value={item}>
                        {item}
                      </Option>
                    ))}
                  </Select>
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout}>
                {getFieldDecorator("time")(
                  <RangePicker showTime format="YYYY-MM-DD HH:mm:ss" />
                )}
              </FormItem>
            </Col>
            <Col span={24}>
              <FormItem className="oms-condition-operate">
                <Qbtn type="primary" onClick={this.handleSubmit.bind(this)}>
                  
                </Qbtn>
              </FormItem>
            </Col>
          </Row>
        </Form>
      </div>
    );
  }
}
const SearchForm = Form.create({
  mapPropsToFields(props) {
    return {
      channelCode: Form.createFormField({
        value: props.shopId,
      }),
    };
  },
})(Search);
export default SearchForm;
