import { Form } from "antd";
import { GetGoodDetailApi } from "api/home/Bgoods";
const formItemLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 12 }
};
class BgoodsDetail extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      infos: {}
    };
  }
  componentDidMount() {
    const {id} = this.props.match.params;
    GetGoodDetailApi({ sku_id:id, saleRange: "b" }).then(res => {
      this.setState({
        infos: res.result
      });
    });
  }
  render() {
    const {infos} = this.state
    return (
      <div className="oms-common-addEdit-pages">
        <Form {...formItemLayout}>
          <Form.Item label="spu">{infos.spuCode}</Form.Item>
          <Form.Item label="">{infos.supplierName}</Form.Item>
          <Form.Item label="sku">{infos.skuCode}</Form.Item>
          <Form.Item label="sku">{infos.productName}</Form.Item>
          <Form.Item label="sku">{infos.salesAttributeName}</Form.Item>
          <Form.Item label="sku">{infos.barCode}</Form.Item>
          <Form.Item label="">{infos.shelfLife}</Form.Item>
          <Form.Item label="">{infos.taxNo}</Form.Item>
          <Form.Item label="">{infos.weight}</Form.Item>
          <Form.Item label="">{infos.length}</Form.Item>
          <Form.Item label="">{infos.wide}</Form.Item>
          <Form.Item label="">{infos.high}</Form.Item>
          <Form.Item label="B()">{infos.spuCode}</Form.Item>
          <Form.Item label="">{infos.taxRate}</Form.Item>
          <Form.Item label="()">{infos.purchasePrice}</Form.Item>
          <Form.Item label="()">{infos.goldCardPrice}</Form.Item>
          <Form.Item label="()">{infos.silverCardPrice}</Form.Item>
          <Form.Item label="">{infos.upperStatus==1?'':''}</Form.Item>
          <Form.Item label=""><img src={infos.image} style={{width:'100px',height:'100px'}}/></Form.Item>
          <Form.Item label="">{infos.skuRemark}</Form.Item>
        </Form>
      </div>
    );
  }
}
export default BgoodsDetail;
