import moment from "moment";
import {Link} from 'react-router-dom'
const Columns = [
  { title: "", dataIndex: "warehouseCode", key: "1" },
  {
    title: "",
    dataIndex: "warehouseName",
    key: "2"
  },
  {
    title: "",
    dataIndex: "warehouseType",
    key: "3",
    render: (text, record, index) => {
      return (
        <span>
          {text && text == 1 ? "" : text == 2 ? "" : ""}
        </span>
      );
    }
  },
  {
    title: "",
    dataIndex: "priority",
    key: "4",
    render:(text,record,index)=>{
      return<span>{text&&text==1?'':(text==2?'':'')}</span>
    }
  },
  {
    title: "",
    dataIndex: "createTime",
    key: "5",
    render: (text, record, index) => {
      return <span>{text && moment(text).format("YYYY-MM-DD HH:mm:ss")}</span>;
    }
  },
  {
    title: "",
    dataIndex: "",
    key: "6",
    render: (text, record, index) => {
      return <Link to={`/account/storeAdd/${record.warehouseCode}`}></Link>
    }
  }
];

export default Columns;
