import moment from "moment";
import { Button } from 'antd';
import { Link } from "react-router-dom";
import { Qbtn } from 'common';
import { statusOption } from '../optionMap';

const columns = [
  {
    title: "",
    dataIndex: "name",
  },
  {
    title: "",
    dataIndex: "phone",
  },
  {
    title: "",
    dataIndex: "address",
    render:(text, record,index)=>{
      return<div>{record.province}{record.city}{record.area}</div>
    }
  },
  {
    title: "",
    dataIndex: "status",
    render:(text,record,index)=> {
      return <div>{
        statusOption.map((el,index)=> (
          <span key={index}>{el.key==record.status&&el.value}</span>
        ))
      }</div>
    }
  },
  {
    title: "",
    dataIndex: "action",
    render:(text,record,index)=>{
      return <div className="handle-action">
          {
            (record.status!=4&&record.status!=7)&&
            <span className="handle-btn-item" type="primary" onClick={()=>record.onOperateClick('upload')}></span>
          }
          {
            record.status!=7&&
            <span className="handle-btn-item" type="primary" onClick={()=>record.onOperateClick('editRecord')}></span>
          }
          <Link className="handle-btn-item" to={`/account/investmentInfo/${record.id}`}></Link>
          {
            (record.status==3||record.status==6)&&
            <span className="handle-btn-item" type="primary" onClick={()=>record.onOperateClick('audit')}></span>
          }
        </div>
    }
  }
];
const columnsInfo=[
  {
    title: "",
    dataIndex: "approachDate",
    render:(text,record,index)=>{
      return<span>{moment(record.approachDate).format('YYYY-MM-DD')}</span>
    }
  },{
    title: "",
    dataIndex: "approachPeople",
  },{
    title: "",
    dataIndex: "approachAddress",
  },{
    title: "",
    dataIndex: "updateStatus",
    render:(text,record,index)=>{
      return <div>{
        statusOption.map((el,index)=> (
          <span key={index}>{el.key==record.updateStatus&&el.value}</span>
        ))
      }</div>
    }
  },{
    title: "",
    dataIndex: "approachMemo",
  }]
export  {
  columns,
  columnsInfo
};
