import React, { Component } from "react";
import { Modal, Input,Form } from "antd";
const FormItem = Form.Item;
const TextArea = Input.TextArea;

class PassModal extends Component {
  constructor(props){
    super(props)
  }
  resetFields=()=>{
    this.props.form.resetFields()
  }
  onOk=()=>{
    if(this.props.status==4){
      this.props.onOk({},this.resetFields)
    }else{
      this.props.form.validateFieldsAndScroll((err,values)=>{
        if(!err){
          this.props.onOk(values,this.resetFields)
        };
      })
    };
  }
  onCancel=()=>{
    this.props.onCancel(this.resetFields)
  }
  render() {
    const { getFieldDecorator } = this.props.form;
    const { status,visible } = this.props;
    return (
      <div>
        <Modal
          title={status == 4 ? "" : ""}
          visible={visible}
          onOk={this.onOk}
          onCancel={this.onCancel}
          okText=""
          cancelText=""
        >
          <div>
            {status == 4 ? (
              <p></p>
            ) : (
              <Form>
                <FormItem
                  label=""
                  labelCol={{span:4}}
                  wrapperCol={{span:12}}
                >
                  {getFieldDecorator("remark", {
                    rules: [{ required: true, message: "" }]
                  })(<TextArea placeholder="" rows={5} />)}
                </FormItem>
              </Form>
            )}
          </div>
        </Modal>
      </div>
    );
  }
}
const PassModals = Form.create({})(PassModal)
export default PassModals
