import { Link } from "react-router-dom";
const Columns = [
  {
    title: "",
    dataIndex: "name",
    key: "2"
  },
  {
    title: "",
    dataIndex: "jName",
    key: "3"
  },
  {
    title: "",
    dataIndex: "accountsType",
    key: "4",
    render:(text,record,index)=>(
      text==1?'':(text==2?'':'')
    )
  },
  {
    title: "",
    dataIndex: "cooperationStatus",
    key: "5",
    render:(text,record,index)=>(
      text==1?'':(text==2?'':'')
    )
  },
  {
    title: "",
    dataIndex: "",
    key: "6",
    render: (text, record, index) => {
      return record.cooperationStatus == 2 ? (
        <a onClick={()=>record.onOperateClick(1)}></a>
      ) : record.cooperationStatus == 1 ? (
        <div>
          <Link to={`/account/addSupplier/${record.id}`}></Link>
          <a onClick={()=>record.onOperateClick(3)}></a>
        </div>
      ) : (
        ""
      );
    }
  }
];

export default Columns;
