import { Form, Input, Select, message, Radio, DatePicker } from "antd";
import {
  ShopInfosApi,
  updataRuleApi
} from "api/home/BaseGoodsCenter/ShopManage";

import { Qbtn } from "common";
let FormItem = Form.Item;
const { RangePicker } = DatePicker;
const { Option } = Select;
const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 4 }
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 12 }
  }
};

class AddShop extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      infos: {},
      fileList: [],
      shopLists: [],
      checkValue: false
    };
  }
  componentDidMount() {
    const { id } = this.props.match.params;
    if (id) {
      ShopInfosApi({ id }).then(res => {
        const { channelPic } = res.result;
        const fileList = [
          {
            uid: "-1",
            name: "image.png",
            status: "done",
            url: res.result.image
          }
        ];
        this.setState({
          infos: res.result,
          fileList
        });
      });
    }
  }
  handleSubmit = () => {
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        const { id } = this.props.match.params;
        updataRuleApi({ id, ...values }).then(res => {
          message.success("");
          this.props.history.push("/account/channelManage");
        });
      }
    });
  };
  updateFileList = fileList => {
    this.setState({ fileList });
  };
  goBack = () => {
    this.props.history.push("/account/channelManage");
  };
  onChange = value => {
    this.setState({
      checkValue: value
    });
  };
  render() {
    const { infos, fileList, shopLists, checkValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    console.log(shopLists);
    return (
      <div className="oms-common-addEdit-pages">
        <Form {...formItemLayout}>
          <Form.Item label="">
            {getFieldDecorator("openingBank", {
              initialValue: infos.openingBank,
              rules: [{ required: true, message: "" }]
            })(<Input placeholder="" autoComplete="off" />)}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("bankCardNo", {
              initialValue: infos.bankCardNo,
              rules: [{ required: true, message: "" }]
            })(<Input placeholder="" autoComplete="off" />)}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("openingName", {
              initialValue: infos.openingName,
              rules: [{ required: true, message: "" }]
            })(<Input placeholder="" autoComplete="off" />)}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("channelPhone", {
              initialValue: infos.channelPhone,
              rules: [{ required: true, message: "" }]
            })(<Input placeholder="" autoComplete="off" />)}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("servicePhone", {
              initialValue: infos.servicePhone,
              rules: [{ required: true, message: "" }]
            })(<Input placeholder="" autoComplete="off" />)}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("weiPayStatus", {
              initialValue: infos.weiPayStatus,
              rules: [{ required: true, message: "" }]
            })(
              <Radio.Group>
                <Radio value={1}></Radio>
                <Radio value={0}></Radio>
              </Radio.Group>
            )}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("alipayStatus", {
              initialValue: infos.alipayStatus,
              rules: [{ required: true, message: "" }]
            })(
              <Radio.Group>
                <Radio value={1}></Radio>
                <Radio value={0}></Radio>
              </Radio.Group>
            )}
          </Form.Item>
          <Form.Item label="CApp">
            {getFieldDecorator("cAppStatus", {
              initialValue: infos.cAppStatus,
              rules: [{ required: true, message: "" }]
            })(
              <Radio.Group>
                <Radio value={1}></Radio>
                <Radio value={0}></Radio>
              </Radio.Group>
            )}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("channelType", {
              initialValue: infos.channelType,
              rules: [{ required: true, message: "" }]
            })(
              <Select placeholder="" allowClear={true}>
                <Option value={1}></Option>
                <Option value={2}></Option>
                <Option value={3}></Option>
              </Select>
            )}
          </Form.Item>

          <Form.Item label="">
            {getFieldDecorator("divideIntoA", {
              initialValue: infos.divideIntoA,
              rules: [{ required: true, message: "" }]
            })(
              <Input suffix="%" placeholder="" autoComplete="off" />
            )}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("divideIntoB", {
              initialValue: infos.divideIntoB,
              rules: [{ required: true, message: "" }]
            })(
              <Input
                suffix="%"
                placeholder=""
                autoComplete="off"
              />
            )}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("businessHours", {
              initialValue: infos.businessHours,
              rules: [{ required: true, message: "" }]
            })(<RangePicker />)}
          </Form.Item>
          <div className="handle-operate-save-action">
            <Qbtn onClick={this.goBack}></Qbtn>
            <Qbtn onClick={this.handleSubmit}></Qbtn>
          </div>
        </Form>
      </div>
    );
  }
}
const AddShops = Form.create({})(AddShop);
export default AddShops;
