import { Form } from "antd";
import { GetInfoApi } from "api/home/Brand";
const formItemLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 12 }
};
class BrandInfo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      infos: {}
    };
  }
  componentDidMount() {
    const {id} = this.props.match.params;
    GetInfoApi({ brandId:id }).then(res => {
      this.setState({
        infos: res.result
      });
    });
  }
  render() {
    const {infos} = this.state
    return (
      <div className="oms-common-addEdit-pages">
        <Form {...formItemLayout}>
          <Form.Item label="">{infos.brandNameCn}</Form.Item>
          <Form.Item label="">{infos.brandNameEn}</Form.Item>
          <Form.Item label="">{infos.status==1?'':''}</Form.Item>
          <Form.Item label="">{infos.brandCountry}</Form.Item>
          <Form.Item label="logo"><img src={infos.logo} style={{'width':'100px','height':'100px'}}/></Form.Item>
          <Form.Item label="">{infos.isSq==1?'':''}</Form.Item>
          <Form.Item label="">{infos.brandIntroduce}</Form.Item>
        </Form>
      </div>
    );
  }
}
export default BrandInfo;
