import { Form, Input, Radio, message } from "antd";
import { GetGoodDetailApi, saveGoodApi } from "api/home/Bgoods";
import QupLoadImgLimt from "common/QupLoadImgLimt";
import { Qbtn } from "common";
let FormItem = Form.Item;
const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 4 }
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 12 }
  }
};
class BgoodsAdd extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      infos: {},
      fileList: []
    };
  }
  componentDidMount() {
    const { id } = this.props.match.params;
    GetGoodDetailApi({ sku_id: id, saleRange: "b" }).then(res => {
      const fileList = [
        {
          uid: "-1",
          name: "image.png",
          status: "done",
          url: res.result.image
        }
      ];
      this.setState({
        infos: res.result,
        fileList
      });
    });
  }
  handleSubmit = () => {
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.image = this.state.fileList[0].response.result;
        saveGoodApi({ saleRange: "b", id: "17769", ...values }).then(res => {
          message.success("");
          this.props.history.push("/account/Bsite");
        });
      }
    });
  };
  upDateList = fileList => {
    this.setState({ fileList });
  };
  goBack = () => {
    this.props.history.push("/account/Bsite");
  };
  render() {
    const { infos, fileList } = this.state;
    const { getFieldDecorator } = this.props.form;
    return (
      <div className="oms-common-addEdit-pages">
        <Form {...formItemLayout}>
          <Form.Item label="spu">{infos.spuCode}</Form.Item>
          <Form.Item label="">{infos.supplierName}</Form.Item>
          <Form.Item label="sku">{infos.skuCode}</Form.Item>
          <Form.Item label="sku">{infos.productName}</Form.Item>
          <Form.Item label="sku">{infos.salesAttributeName}</Form.Item>
          <Form.Item label="sku">{infos.barCode}</Form.Item>
          <Form.Item label="">{infos.shelfLife}</Form.Item>
          <Form.Item label="">{infos.taxNo}</Form.Item>
          <Form.Item label="">
            {getFieldDecorator("weight", {
              initialValue: infos.weight,
              rules: [{ required: true, message: "" }]
            })(<Input placeholder="" autoComplete="off" />)}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("length", {
              initialValue: infos.length,
              rules: [{ required: true, message: "" }]
            })(<Input placeholder="" autoComplete="off" />)}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("wide", {
              initialValue: infos.wide,
              rules: [{ required: true, message: "" }]
            })(<Input placeholder="" autoComplete="off" />)}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("high", {
              initialValue: infos.high,
              rules: [{ required: true, message: "" }]
            })(<Input placeholder="" autoComplete="off" />)}
          </Form.Item>
          <Form.Item label="B()">
            {getFieldDecorator("bPrice", {
              initialValue: infos.bPrice,
              rules: [{ required: true, message: "B" }]
            })(<Input placeholder="B" autoComplete="off" />)}
          </Form.Item>
          <Form.Item label="B">
            {getFieldDecorator("taxRate", {
              initialValue: infos.taxRate,
              rules: [{ required: true, message: "B" }]
            })(<Input placeholder="B" autoComplete="off" />)}
          </Form.Item>
          <Form.Item label="()">
            {getFieldDecorator("purchasePrice", {
              initialValue: infos.purchasePrice,
              rules: [{ required: true, message: "" }]
            })(<Input placeholder="" autoComplete="off" />)}
          </Form.Item>
          <Form.Item label="()">
            {getFieldDecorator("goldCardPrice", {
              initialValue: infos.goldCardPrice,
              rules: [{ required: true, message: "" }]
            })(<Input placeholder="" autoComplete="off" />)}
          </Form.Item>
          <Form.Item label="()">
            {getFieldDecorator("silverCardPrice", {
              initialValue: infos.silverCardPrice,
              rules: [{ required: true, message: "" }]
            })(<Input placeholder="" autoComplete="off" />)}
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("upperStatus", {
              initialValue: infos.upperStatus
            })(
              <Radio.Group>
                <Radio value={1}></Radio>
                <Radio value={0}></Radio>
              </Radio.Group>
            )}
          </Form.Item>
          <Form.Item label="">
            <QupLoadImgLimt upDateList={this.upDateList} fileList={fileList} />
          </Form.Item>
          <Form.Item label="">
            {getFieldDecorator("skuRemark", {
              initialValue: infos.skuRemark
            })(<Input.TextArea rowSpan={6} colSpan={5} />)}
          </Form.Item>
          <div className="handle-operate-save-action">
            <Qbtn onClick={this.goBack}></Qbtn>
            <Qbtn onClick={this.handleSubmit}></Qbtn>
          </div>
        </Form>
      </div>
    );
  }
}
const BgoodsAdds = Form.create({})(BgoodsAdd);
export default BgoodsAdds;
