import { Form, Input, Select, DatePicker, Row, Col } from "antd";
import { BaseFilter, Qbtn } from "common";
import { GetCategoryApi } from "api/home/BaseGoods";
const FormItem = Form.Item;
const { Option } = Select;
const { RangePicker } = DatePicker;
class Search extends BaseFilter {
  constructor(props) {
    super(props);
    this.state = {
      catagoryList: [],
      catagoryList2: []
    };
  }
  componentDidMount = () => {
    this.initPage();
  };
  initPage = () => {
    GetCategoryApi({ level: 1, parentId: "" }).then(res => {
      this.setState({
        catagoryList: res.result
      });
    });
  };
  onChange = value => {
    this.props.form.resetFields(['categoryCode2'])
    this.setState({catagoryList2:[]});
    if (value) {
      GetCategoryApi({ level: -1, parentId: value }).then(res => {
        this.setState({
          catagoryList2: res.result || []
        });
      });
    }
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { catagoryList, catagoryList2 } = this.state;
    return (
      <div className="qtoolOms-condition">
        <Form className="serach-common-form">
          <Row gutter={24}>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout}>
                {getFieldDecorator("productName")(
                  <Input placeholder="" autoComplete="off" />
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout}>
                {getFieldDecorator("supplierName")(
                  <Input placeholder="" autoComplete="off" />
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="spu" {...this.formItemLayout}>
                {getFieldDecorator("spuCode")(
                  <Input placeholder="spu" autoComplete="off" />
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="sku" {...this.formItemLayout}>
                {getFieldDecorator("skuCode")(
                  <Input placeholder="sku" autoComplete="off" />
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout}>
                {getFieldDecorator("productName")(
                  <Input placeholder="" autoComplete="off" />
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout}>
                {getFieldDecorator("categoryCode1", {
                  onChange: this.onChange
                })(
                  <Select placeholder="" allowClear={true}>
                    {catagoryList.map(item => (
                      <Option value={item.id} key={item.id}>
                        {item.categoryName}
                      </Option>
                    ))}
                  </Select>
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout}>
                {getFieldDecorator("categoryCode2")(
                  <Select
                    placeholder=""
                    disabled={!catagoryList2.length > 0}
                    allowClear={true}
                  >
                    {catagoryList2.map(item => (
                      <Option value={item.id} key={item.id}>
                        {item.categoryName}
                      </Option>
                    ))}
                  </Select>
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout}>
                {getFieldDecorator("productNature")(
                  <Select placeholder="" allowClear={true}>
                    <Option value={1}></Option>
                    <Option value={2}></Option>
                  </Select>
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout}>
                {getFieldDecorator("productType")(
                  <Select placeholder="" allowClear={true}>
                    <Option value={1}></Option>
                    <Option value={2}></Option>
                  </Select>
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout}>
                {getFieldDecorator("sendType")(
                  <Select placeholder="" allowClear={true}>
                    <Option value={1}></Option>
                    <Option value={2}></Option>
                  </Select>
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout}>
                {getFieldDecorator("status")(
                  <Select placeholder="" allowClear={true}>
                    <Option value={0}></Option>
                    <Option value={1}></Option>
                    <Option value={2}></Option>
                    <Option value={3}></Option>
                  </Select>
                )}
              </FormItem>
            </Col>
            <Col {...this.colspans}>
              <FormItem label="" {...this.formItemLayout2}>
                {getFieldDecorator("time", {
                  initialValue: this.initTime
                })(
                  <RangePicker
                    placeholder={this.placeholder}
                    format={this.formatType}
                    showTime
                  />
                )}
              </FormItem>
            </Col>
            <Col span={24}>
              <FormItem className="oms-condition-operate">
                <Qbtn type="primary" onClick={this.handleSubmit.bind(this)}>
                  
                </Qbtn>
              </FormItem>
            </Col>
          </Row>
        </Form>
      </div>
    );
  }
}
const SearchForm = Form.create({})(Search);
export default SearchForm;
